/*-----------------------------------------
	1. General
-------------------------------------------*/
.container {
  max-width: 1200px;
}

html,
body {
  font-family: Poppins;
  color: #1c303e;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Poppins;
  color: #222;
  line-height: 1.3;
  margin: 0px;
}

h1,
h2,
h3,
h4 {
  font-weight: 700;
}

h5,
h6 {
  font-weight: 600;
}

img {
  max-width: 100%;
  height: auto;
  transition-duration: .3s;
}

a {
  color: #222;
  transition-duration: .3s;
}

a:active,
a:hover,
a:focus {
  outline: 0 none;
  text-decoration: none;
}

button {
  cursor: pointer;
  background: none;
  outline: 0 none !important;
  border: none;
}

iframe {
  border: 0px;
}

input,
textarea {
  outline: none;
