/*-----------------------------------------28. Chart JS-------------------------------------------*/

.chartjs {
    height: 320px;
}


/*-----------------------------------------29. Timeline-------------------------------------------*/

.timeline {
    position: relative;
    max-width: 100%;
    margin: 0 auto;
}

.timeline::after {
    content: '';
    position: absolute;
    width: 2px;
    background-color: #eee;
    top: 0;
    bottom: 0;
    left: 50%;
    margin-left: -3px;
}


/* Container around content */

.timeline-container {
    padding: 10px 40px;
    position: relative;
    background-color: inherit;
    width: 50%;
}


/* The circles on the timeline */

.timeline-container::after {
    content: '';
    position: absolute;
    width: 12px;
    height: 12px;
    right: -4px;
    background-color: #fff;
    border: 2px solid #ef4736;
    top: 15px;
    border-radius: 50%;
    z-index: 1;
}

.timeline-left {
    left: 0;
}

.timeline-right {
    left: 50%;
}


/* Add arrows to the left container (pointing right) */

.timeline-left::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 22px;
    width: 0;
    z-index: 1;
    right: 30px;
    border: medium solid white;
    border-width: 10px 0 10px 10px;
    border-color: transparent transparent transparent white;
}


/* Add arrows to the right container (pointing left) */

.timeline-right::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 22px;
    width: 0;
    z-index: 1;
    left: 10px;
    border: 2px solid white;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
}

.timeline-right::after {
    left: -8px;
}


/* The actual content */

.timeline-content {
    padding: 0px 0px 50px 0px;
    background-color: white;
    position: relative;
    border-radius: 6px;
}

.timeline-content-inner {
    background: #f7f7f7;
    border-radius: 10px;
    padding: 20px 30px 30px 30px;
}

.timeline-content-inner p {
    font-size: 14px;
    color: #999;
    line-height: 1.7;
    font-weight: 400;
    margin-bottom: 0px;
}

.timeline-content h3 {
    display: block;
    color: #222;
    font-size: 16px;
    font-weight: 500;
    margin: 7px 0px;
}

.timeline h2 {
    display: block;
    color: #ef4736;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 15px;
}

.timeline-left-all h2 {
    width: 50%;
    color: #ef4736;
    margin-left: 50%;
    padding-left: 30px;
    margin-bottom: -35px;
}

.timeline-right-all h2 {
