/*-----------------------------------------37. Shop-------------------------------------------*/


/*--------- Style 1 ----------*/

.shop-1 {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: auto;
    text-align: center;
    cursor: pointer;
    margin: 15px 0px;
    transition-duration: 0.4s;
}

.shop-1:hover {
    box-shadow: 0px 3px 22px 0px rgba(50, 50, 50, 0.2);
}

.shop-1 img {
    position: relative;
    display: block;
    width: 100%;
    height: auto;
}

.shop-1 {
    background: transparent;
    transition-duration: 0.4s;
}

.shop-1:hover {
    background: #000;
}

.shop-1 h6 {
    position: absolute;
    bottom: 10px;
    right: 0;
    left: 0;
    font-size: 18px;
    color: #fff;
    padding: 30px 5px;
}

.shop-1 h6:hover {
    color: #fff;
}

.shop-1 p {
    position: absolute;
    bottom: -15px;
    right: 0;
    left: 0;
    font-size: 15px;
    color: #fff;
    padding: 25px 30px;
}

.shop-1 p span {
    text-decoration: line-through;
    color: #aaa;
    padding: 0px 5px;
}

.shop-1 h6 {
    text-transform: none;
    opacity: 0;
    transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
    transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
    -webkit-transform: translate3d(0, 50px, 0);
    transform: translate3d(0, 50px, 0);
}

.shop-1 p {
    text-transform: none;
    opacity: 0;
    transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
    transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
    -webkit-transform: translate3d(0, 50px, 0);
    transform: translate3d(0, 50px, 0);
}

.shop-1:hover img {
    -webkit-transform: translate3d(0, -80px, 0);
    transform: translate3d(0, -80px, 0);
}

.shop-1:hover h6 {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.shop-1:hover p {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}


/*Sale Hot*/

.shop-classic-sale-img {
    display: block;
    float: right;
    position: absolute;
    text-align: center;
    background: #45bcde;
    left: 20px;
    top: 5px;
    min-width: 60px;
    padding: 4px 0px;
    margin: 10px 0px 15px 0px;
    transition-duration: 0.4s;
}

.shop-1:hover .shop-classic-sale-img {
    box-shadow: 2px 2px 30px 0px rgba(50, 50, 50, 0.5);
}

.shop-classic-sale-img h4 {
    text-transform: uppercase;
    color: #fff;
    font-size: 12px;
    font-weight: 600;
    letter-spacing: 2px;
    margin-top: 0px;
}

.shop-classic-hot-img {
    display: block;
    float: right;
    position: absolute;
    text-align: center;
    background: #ef4736;
    left: 20px;
    top: 5px;
    min-width: 60px;
    padding: 4px 0px;
    margin: 10px 0px 15px 0px;
}

.shop-classic-hot-img h4 {
    text-transform: uppercase;
    color: #fff;
    font-size: 12px;
    font-weight: 600;
    letter-spacing: 1px;
    margin-top: 0px;
}

@media(max-width: 1195px) {
    .shop-1 h6 {
        position: absolute;
        bottom: 13px;
        right: 0;
        left: 0;
        font-size: 16px;
        padding: 20px 20px;
    }
}

@media(max-width: 768px) {
    .shop-1 h6 {
        position: absolute;
        bottom: 15px;
        right: 0;
        left: 0;
        font-size: 18px;
        padding: 25px 25px;
    }
}

.shop-product {
    margin-top: 10px;
}

.shop-product img {
    width: 100%;
    border-radius: 5px;
}

.shop-product h3 {
    line-height: 1.3;
    margin-top: -10px;
}

.shop-product h3 a {
    display: inline-block;
    color: #333;
    font-size: 15px;
    font-weight: 500;
}

.shop-product h3 a:hover {
    color: #ef4736;
}

.shop-product h6 {
    color: #999;
    font-size: 14px;
    font-weight: 500;
    margin-top: -3px !important;
}

.shop-product h6 {
    color: #999;
    font-size: 14px;
    font-weight: 500;
    margin-top: 5px;
}

@media (max-width: 991px) {
    .price-star ul {
        display: block;
        float: none;
    }

    .over h5 {
        font-size: 14px;
    }

    .over {
        position: absolute;
        bottom: 85px;
    }

    .shop-2:hover .over {
        height: 15%;
    }
}

