.price-star {
    text-align: left;
    margin-top: 5px;
}

.price-star i {
    font-size: 12px;
    color: #ef4736;
}

.price-star h6 {
    color: #333;
    display: inline-block;
    font-size: 13px;
    font-weight: 500;
    letter-spacing: 1px;
}

.price-star h6 span {
    color: #777;
    text-decoration: line-through;
    font-size: 10px;
    font-weight: 500;
}

.price-star ul {
    display: block;
}

.price-star ul li {
    display: inline-block;
    color: #ef4736;
    margin: 0px;
}

.single-product {
    padding-left: 30px;
}

@media screen and (max-width:767px) {
    .single-product {
        padding-left: 0px;
        margin-top: 30px;
    }
}

.single-product h2 {
    font-size: 25px;
    font-weight: 600;
    color: #222;
}

.single-product p {
    font-size: 15px;
    font-weight: 400;
    line-height: 180%;
    color: #999;
    margin-top: 20px;
}

.product-review-area {
    margin-top: 15px;
}

.product-review-area i {
    color: #f9d100;
    font-size: 18px;
    margin-right: 2px;
}

.product-review-area span {
    color: #111;
    font-size: 14px;
    margin-left: 10px;
}

