    margin: 15px auto;
    font-style: italic;
    font-size: 14px;
    font-weight: 500;
    color: #222;
    padding: 20px;
    border-left: 4px solid #ef4736;
    line-height: 1.6;
    position: relative;
    background: #f1f1f1;
}

#search-input {
    border: solid 1px #eee;
    border-radius: 5px;
    background-color: transparent;
    padding: 5px 3px;
}

#search-input input {
    font-size: 13px;
    font-weight: 500;
    border: 0;
    box-shadow: none;
}

#search-input button {
    background: none;
    box-shadow: none;
    border: 0;
    border-radius: 0;
    outline: none !important;
    padding: 0 11px 0 13px;
}

#search-input button i {
    color: #ef4736;
    font-size: 16px;
    margin: 8px 3px 0px 0px;
    transition-duration: .3s;
}

#search-input button:hover i {
    color: #777;
}


/*Category*/

.blog-post-categories ul li {
    background: #fff;
    border: 1px solid #f7f7f7;
    margin: 5px 0px;
    transition-duration: .4s;
}

.blog-post-categories ul li:hover {
    background: #f5f5f5;
}

.blog-post-categories ul li a {
    display: block;
    color: #444;
    font-size: 14px;
    font-weight: 500;
    padding: 15px;
}

.blog-post-categories ul li a:hover {
    color: #ef4736;
}

.blog-post-categories ul li a span {
    color: #444;
    font-size: 12px;
    float: right;
}

.blog-post-categories ul li:hover a span {
    color: #ef4736;
}


/*Top News*/

.top-news-info {
    margin: 20px 0px;
}

.top-news img {
    width: 100%;
    border-radius: 5px;
}

.top-news-info h3 a {
    display: inline-block;
    color: #333;
    font-size: 14px;
    font-weight: 500;
}

.top-news-info h3 a:hover {
    color: #ef4736;
}

.top-news-info h6 {
    color: #999;
    font-size: 14px;
    font-weight: 500;
    margin-top: 5px;
}


/*Tags*/

.recent-post-tags a {
    margin: 3px 2px;
    border-radius: 30px;
}

.dark-button-bordered {
    background: transparent;
    color: #222;
    font-size: 11px;
    font-weight: 500;
    border: 2px solid #ddd;
    border-radius: 50px;
    padding: 10px 15px 10px 15px;
    transition-duration: .3s;
}

.dark-button-bordered:hover {
    color: #fff;
    background: #222;
    border-color: #222;
}


/*Archive*/

.archive-box ul li {
    border-bottom: 1px solid #eee;
}

.archive-box ul li a {
    display: inline-block;
    color: #222;
    font-size: 14px;
    font-weight: 500;
    line-height: 160%;
    padding: 15px 0px 15px 5px;
    transition-duration: .4s;
}

.archive-box ul li a:hover {
    color: #ef4736;
}

.archive-box ul li span {
    float: right;
    color: #999;
    font-weight: 600;
    font-size: 13px;
    margin-right: 10px;
    margin-top: 15px;
}


/* social links*/

.blog-post-follow ul {
    display: block;
}

.blog-post-follow ul li {
    display: inline-block;
    align-items: center;
    text-align: center;
    border-radius: 50px;
    margin: 4px 2px;
    background: #333;
    transition-duration: .4s;
}

.blog-post-follow ul li a i {
    width: 40px;
    height: 40px;
    color: #fff;
    font-size: 17px;
    padding-top: 11px;
}

.blog-post-follow ul li:hover {
    background: #ef4736;
}


/*Comments*/

.blog-comments {
    background: #fff;
    border-radius: 5px;
}

.blog-comments h3 {
    font-size: 20px;
    font-weight: 500;
    color: #222;
    margin-top: 30px;
}

.blog-comment-user {
    margin-top: 30px;
}

.blog-comment-user img {
    border-radius: 5px;
}

.blog-comment-user h6 {
    display: inline-block;
    color: #222;
    font-size: 16px;
    margin: 0px;
    margin-right: 15px;
}

.blog-comment-user strong {
    color: #ef4736;
    font-size: 12px;
    font-weight: 600;
    float: right;
}

.blog-comment-user p {
    color: #777;
    font-size: 13px;
    font-weight: 500;
    line-height: 1.7;
    margin-top: 10px;
}

.comment-block {
    background: #f5f5f5;
    border-radius: 5px;
    padding: 20px;
    transition-duration: 0.3s;
}

.blog-widget-title {
    color: #222;
    font-size: 20px;
    font-weight: 500;
    margin-top: 35px;
}

.blog-widget-title:after {
    content: '';
    display: block;
    width: 65px;
    max-width: 100%;
    height: 2px;
    background: #ef4736;
    margin-top: 10px;
}


/*-----------------------------------------44. Revolution Slider-------------------------------------------*/

.rev_slider_wrapper {
    overflow: hidden;
}

.bg-arrows .tparrows {
    background: rgba(0, 0, 0, .2);
    border-radius: 50px;
    width: 60px;
    height: 60px;
    padding-top: 10px;
    transition-duration: 0.3s;
}

.bg-arrows .tparrows:before {
    font-size: 17px
}

.tparrows {
    background: rgba(0, 0, 0, .0);
    border-radius: 50px;
    width: 60px;
    height: 60px;
    padding-top: 10px;
    transition-duration: 0.3s;
}

.tparrows:hover {
    background: rgba(0, 0, 0, .3);
}

.tparrows:before {
    font-size: 23px
}


/*-----------------------------------------45. Footer-------------------------------------------*/

.footer-1 {
    background: #222;
    padding: 80px 0px 0px 0px;
}

.footer-1 h2 {
    color: #fff;
    font-size: 22px;
    font-weight: 500;
}

@media (max-width: 767px) {
