}

.footer-1 .social-links-footer a {
    color: #fff;
    font-size: 17px;
}

.footer-nav li {
    margin: 5px 0px;
}

.footer-nav a {
    color: #fff;
    font-size: 14px;
}

.footer-nav a:hover {
    color: #ef4736;
}

.footer-news li {
    border-bottom: 1px solid #444;
    padding: 18px 0px;
}

.footer-news li:first-child {
    padding-top: 0px;
}

.footer-news li:last-child {
    border: none
}

.footer-news a {
    color: #fff;
    font-size: 14px;
}

.footer-news a:hover {
    color: #ef4736;
}

.footer-news strong {
    display: block;
    color: #999;
    font-weight: 400;
    font-size: 13px;
    margin-top: 5px;
}

.footer-news strong i {
    color: #ef4736;
    margin-right: 5px;
}

.footer-subscribe-form input {
    background: #fff;
